:root {
    --main-bg-color: #303C6C;
    --secondary-bg-color: #B4DFE5;
    --primary-color: #FBE8A6;
    --secondary-color: #F4976C;
    --third-color: #D2FDFF;
}

/* --- button styling -- */
.btn {
    display: inline-block;
    margin: 6px;
    font-size: inherit;
    line-height: 1.42;
    padding: 0.8em 1.6em;
    font-weight: normal;
    border-width: 0;
    border-style: solid;
    background: transparent;
    border-radius: 0.2666666667em;
    cursor: pointer;
    font-family: "Booster Next FY", "Avenir Next", Avenir, sans-serif;
    user-select: none;
    vertical-align: bottom;
    transition: box-shadow 0.2s, transform 0.2s, color 0.2s;
    box-shadow: 0 3px 0 0 var(--main-bg-color), 0 4px 4px -1px rgba(0, 0, 0, 0.6), 0 4px 6px 1px rgba(0, 0, 0, 0.3), 0 1px 2px 1px rgba(0, 0, 0, 0) inset, 0 18px 32px -2px rgba(255, 255, 255, 0.1) inset;
    background-color: var(--main-bg-color);
    background-image: linear-gradient(-45deg, var(--primary-color), var(--third-color));
    color: var(--main-bg-color);
    font-weight: bold;
  }
  
  .btn:hover {
    box-shadow: 0 3px 0 0 rgb(131, 144, 197), 0 6px 4px -1px rgba(0, 0, 0, 0.3), 0 4px 16px rgba(0, 0, 0, 0.5), 0 1px 2px 1px rgba(0, 0, 0, 0) inset, 0 18px 32px -2px rgba(255, 255, 255, 0.14) inset;
  }
  .btn:active {
    transform: translateY(3px);
    box-shadow: 0 0px 0 0 var(--main-bg-color), 0 3px 0 0 rgba(0, 0, 0, 0), 0 4px 16px rgba(0, 0, 0, 0), 0 1px 2px 1px rgba(0, 0, 0, 0.5) inset, 0 -18px 32px -2px rgba(255, 255, 255, 0.1) inset;
    transition: 0s;
    color: var(--main-bg-color);
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.3);
  }